@import url("https://fonts.googleapis.com/css2?family=Akaya+Kanadaka&family=Patrick+Hand&display=swap");
* {
  margin: 0px;
  padding: 0px;
  box-sizing: border-box;
  font-family: "Akaya Kanadaka", cursive;
}
.prop-container {
  width: 100%;
}
.prop-child1 {
  background-color: #323131;
}
.prop-child1 h5 {
  text-align: center;
  font-size: 22px;
  color: aliceblue;
  padding: 10px;
}
.prop-child1 .image-card {
  display: flex;
  gap: 20px;
  flex-wrap: wrap;
  justify-content: center;
}
.prop-child1 .image-card ul {
  padding: 0px;
  margin: 0px;
  list-style: none;
}
.prop-child1 .image-card li {
  flex: 1 1 20px;
  max-width: 300px;
}
.prop-child1 .image-card li img {
  width: 100%;
}
.prop-child1 .image-card li p {
  font-family: "Patrick Hand", cursive;
  text-align: center;
  cursor: pointer;
  color: aliceblue;
}
.prop-child1 .image-card li p:hover {
  color: #ff4848;
}
.prop-child1 .image-card li span {
  font-family: "Patrick Hand", cursive;
  text-align: center;
  color: #8a8b8c;
}
.prop-child1 .image-card li div {
  height: 110px;
  overflow-y: scroll;
  scrollbar-base-color: transparent;
}
.prop-child2 {
  background-color: blueviolet;
}

.prop-child3 {
  background-color: burlywood;
}
.image-card {
  display: flex;
}
::-webkit-scrollbar {
  width: 0; /* Remove scrollbar space */
  background: transparent; /* Optional: just make scrollbar invisible */
}
.popup {
  position: fixed;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  z-index: 99;
  background-color: rgba(0, 0, 0, 0.8);
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.popup h2 {
  color: aliceblue;
}
.popup-content {
  background-color: rgb(47 44 54);
  width: 700px;
  height: 500px;
  border-radius: 5px;
  padding: 20px;
}
.popup-close {
  position: absolute;
  top: 25px;
  right: 25px;
  color: white;
  font-weight: 600;
  font-size: 14px;
  cursor: pointer;
  transform-origin: all ease 0.3s;
}
.popup-content button {
  border: none;
  outline: none;
  background-color: #ffb902;
  color: white;
  padding: 12px 22px;
  border-radius: 6px;
  font-family: "Akaya Kanadaka", cursive;
  cursor: pointer;
  font-weight: bold;
  margin-top: 20px;
  transition: all ease 0.3s;
}
.popup-content button:hover {
  background-color: #ff4848;
}
.popup-close:hover {
  color: #ff4848;
}
.popup-content .popup-content-inner img {
  margin-left: 25%;
}
.popup-content .popup-content-inner h2 {
  margin: 20px 0;
}
.popup-content .popup-content-inner h3 {
  color: aliceblue;
  font-size: 14px;
  height: 100px;
  overflow-y: scroll;
}
.loader {
  position: fixed;
  top: 0;
  width: 100%;
  height: 100%;
  background-color: #060606db;
  display: flex;
  align-items: center;
  justify-content: center;
}
.loader-text {
  background: darkgrey;
}

/* context page css */

.context-child1 {
  background-color: #2b2502;
}
.inner-content {
  height: 80vh;
}
.footer {
  margin-top: 20%;
}
